
.pagenotfound-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
}

.pagenotfound-error {
    max-width: 600px;
    width: 100%;
}

.pagenotfound-title {
    font-size: 160px;
    margin: 0;
    font-weight: 900;
    color: rgba(0, 0, 0, 0.1);
}

.pagenotfound-subtitle {
    font-size: 32px;
    margin: 20px 0;
    font-weight: 700;
    color: #000;
}

.pagenotfound-text {
    font-size: 16px;
    color: #666;
    margin: 20px 0 40px;
}

.pagenotfound-btn {
    padding: 12px 24px;
    background-color: #1e90ff;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.pagenotfound-btn:hover {
    color: #ffffff;
    font-weight: bold;
}
