.admit-card-container {
  border: 1px solid black;
  padding: 20px;
  max-width: 700px;
  margin: 0 auto;
}

.admit-header {
  display: flex;
  align-items: center;
  border-bottom: 2px solid black;
  padding-bottom: 10px;
  position: relative;
  margin-bottom: 40px;
}

.admit-logo {
  width: 80px;
  margin-right: 20px;
}

.header-text {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.header-text h1 {
  margin: 0;
  font-size: 24px;
}

.header-text p {
  margin: 5px 0;
  font-size: 14px;
}

.heading-h2 {
  margin-top: 10px;
  text-align: center;
}

.heading-h3 {
  margin-top: 10px;
  text-align: center;
}

/* Flexbox for Admit Card and Room/Token/SL NO alignment */
.admit-info-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px; /* Reduced space */
}

.left-info {
  width: 48%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.right-info {
  width: 48%;
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}

.left-info p,
.right-info p {
  margin: 0;
  padding: 2px 0;
  font-size: 14px;
}

.underline {
  display: inline-block;
  border-bottom: 1px solid black;
  width: 150px; /* Adjusted underline width */
  margin-left: 10px; /* Space between text and underline */
}

.candidate-info {
  width: 100%;
  margin-bottom: 20px;
  border-collapse: collapse;
}

.candidate-info td {
  padding: 8px;
  border: 1px solid black;
  vertical-align: top;
}

.candidate-info td[colspan="3"] {
  width: 75%; /* Adjust width for the 3-column layout */
}

.signature {
  text-align: right;
  margin-top: 30px;
  font-size: 14px;
}

.instructions {
  font-size: 14px;
  line-height: 1.6;
}

.instructions li {
  margin-bottom: 10px;
}

.admit-footer {
  text-align: right;
  margin-top: 30px;
  font-size: 14px;
  font-weight: bold;
}
