body {
  font-family: Arial, sans-serif;
}

.fee-container {
  border: 2px solid black;
  width: 400px;
  padding: 20px;
  margin: 0 auto;
}

.fee-header {
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  padding: 10px 0;
  position: relative;
}

.fee-header img {
  width: 60px;
  position: absolute;
  left: 20px;
}

.fee-header h1 {
  margin: 0;
  text-align: center;
  font-size: 18px;
}

.fee-header p {
  margin: 2px 0;
  text-align: center;
  font-size: 12px;
}

.fee-receipt-title {
  background-color: #c8e088;
  text-align: center;
  font-weight: bold;
  padding: 5px;
  margin: 10px 0;
}

.details {
  margin: 10px 0;
}

.details-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.details p {
  margin: 5px 0;
  font-size: 14px;
}

.details strong {
  font-weight: bold;
}

.receipt-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.receipt-table th,
.receipt-table td {
  border: 1px solid black;
  padding: 8px;
  text-align: left;
}

.receipt-table th {
  background-color: #c8e088;
  text-align: center;
}

.signature {
  text-align: right;
  margin-top: 20px;
  font-size: 14px;
}
