.homework-create {
  margin: 0 auto;
  max-width: 900px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 12px; /* More rounded corners */
  background-color: #f5f5f5; /* Light background for a clean look */
}

.custom-tabs .nav-link.active {
  background-color: #1e90ff; /* Deep blue for active tabs */
  color: white;
  border-radius: 12px 12px 0 0;
}

.custom-tabs .nav-link {
  border-radius: 12px 12px 0 0;
  color: #1e90ff; /* Consistent blue color */
}

.custom-tabs .nav-item {
  margin-right: 10px;
}

.tab-contentt {
  padding: 20px;
  background-color: #ffffff; /* White background for tab content */
  border: 1px solid #ddd;
  border-radius: 0 0 12px 12px; /* Rounded bottom corners */
}

.dropdown-width {
  max-width: 200px; /* Adjust as needed */
}

.homework-rows-container {
  margin-bottom: 20px;
}

.homework-rows-container .row {
  margin-bottom: 15px;
}

.btn-danger {
  margin-top: 10px;
}

.custom-tabs-container {
  background-color: #f7f7f7; /* Light gray for tabs container */
  padding: 10px;
}

.custom-nav-tabs {
  border-bottom: none;
  display: flex;
  justify-content: space-between;
}

.custom-nav-item {
  flex: 1;
}

.custom-nav-link {
  background-color: #e0e0e0; /* Light gray for inactive nav links */
  border: none;
  text-align: center;
  padding: 15px 0;
  color: #555; /* Darker gray text */
  font-weight: bold;
  border-radius: 12px;
  margin-right: 5px;
  display: block;
  font-size: 14px;
  transition: background-color 0.3s, color 0.3s; /* Smooth transition */
}

.custom-nav-link.active {
  background-color: #1e90ff; /* Blue for active nav link */
  color: white;
}

.custom-nav-link i {
  display: block;
  font-size: 24px;
  margin-bottom: 5px;
}

.custom-nav-tabs {
  list-style-type: none; /* Remove default list item markers */
  padding-left: 0; /* Remove default padding */
  margin-bottom: 0; /* Remove default margin at the bottom */
}
.sms-preview-box {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 320px; /* Adjusted width */
  padding: 15px;
  background: linear-gradient(
    135deg,
    #87ceeb,
    #4682b4
  ); /* Light to medium blue gradient */
  border: 2px solid #1e90ff; /* Dodger blue border */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Enhanced shadow for depth */
  z-index: 1000;
  border-radius: 15px; /* Rounded corners */
  overflow: hidden; /* Prevent overflow of rounded corners */
}

.sms-preview-box h6 {
  margin: 0 0 10px 0;
  font-size: 14px; /* Adjusted font size for better readability */
  color: #ffffff; /* White color for text */
  font-weight: bold;
  text-align: center;
  background: rgba(0, 0, 0, 0.3); /* Semi-transparent dark background */
  padding: 10px;
  border-radius: 10px; /* Rounded corners for header */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Subtle shadow for header */
}

.sms-preview-box {
  width: 100%;
  border: 1px solid #1e90ff; /* Dodger blue border */
  background: #ffffff; /* White background for contrast */
  color: #333; /* Dark text for readability */
  font-size: 14px;
  padding: 10px;
  border-radius: 10px; /* Rounded corners */
  resize: none; /* Prevent resizing */
  height: 10px; /* Adjusted height */
  overflow-y: auto; /* Scrollbar for content if necessary */
  overflow-x: hidden; /* Prevent horizontal scrollbar */
}

.areat:focus {
  background-color: #f0f8ff; /* Light blue background on focus */
}
.preview-box {
  position: fixed;
  bottom: 170px;
  right: 20px;
  width: 350px; /* Fixed width */
  height: 250px; /* Fixed height */
  background: linear-gradient(
    135deg,
    #87cefa,
    #4682b4
  ); /* Light to medium blue gradient */
  border: 2px solid #00bfff; /* Deep sky blue border */
  border-radius: 15px; /* Rounded corners */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Enhanced shadow for depth */
  padding: 10px; /* Adjusted padding */
  overflow: hidden; /* Prevent overflow of rounded corners */
  z-index: 9999;
  cursor: move;
  font-family: "Arial", sans-serif; /* Clean font */
}

.preview-box p {
  margin: 0 0 0 0;
  font-size: 16px; /* Adjusted font size */
  color: #ffffff; /* White color for text */
}

.areat {
  width: 100%;
  height: 150px; /* Adjusted height */
  border: 1px solid #00bfff; /* Deep sky blue border */
  background: #ffffff; /* White background */
  color: #333;
  font-size: 14px;
  padding: 10px;
  border-radius: 10px; /* Rounded corners */
  overflow-y: auto; /* Scrollbar for content if necessary */
  overflow-x: hidden; /* Prevent horizontal scrollbar */
}

/* Optional animation on hover */
.preview-box:hover {
  transform: scale(1.03); /* Slight zoom effect */
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.4); /* Enhanced shadow */
}

.circular {
  margin: 0 auto;
  max-width: 900px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 12px; /* More rounded corners */
  background-color: white; /* Light background for a clean look */
}
