#heading {
  text-transform: uppercase;
  color: #007bff;
  font-weight: normal;
}

#msform {
  text-align: center;
  position: relative;
  margin-top: 20px;
  width: 1150px;
}

#msform1 {
  text-align: center;
  position: relative;
  margin-top: 20px;
}

#msform fieldset {
  background: white;
  border: 0 none;
  border-radius: 0.5rem;
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding-bottom: 20px;
  position: relative;
}

.form-card {
  text-align: left;
}

#msform fieldset:not(:first-of-type) {
  display: none;
}

#msform input,
#msform textarea {
  padding: 8px 15px 8px 15px;
  border: 1px solid #ccc;
  border-radius: 0px;
  /* margin-bottom: 10px; */
  margin-top: 2px;
  /* width: 100%; */
  box-sizing: border-box;
  font-family: montserrat;
  color: #2c3e50;
  font-size: 16px;
  letter-spacing: 1px;
}

#msform input:focus,
#msform textarea:focus {
  -moz-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
  border: 1px solid #007bff;
  outline-width: 0;
}

#msform .action-button {
  width: 100px;
  height: 40px;
  background: #007bff;
  font-weight: bold;
  color: white;
  border: 0 none;
  border-radius: 10px;
  cursor: pointer;
  /* padding: 10px 5px; */
  margin: 10px 0px 10px 5px;
  float: inline-end;
}

#msform .action-button:hover,
#msform .action-button:focus {
  background-color: #007bff;
}

#msform .action-button-previous {
  width: 100px;
  height: 40px;
  background: #616161;
  font-weight: bold;
  color: white;
  border: 0 none;
  border-radius: 10px;
  cursor: pointer;
  /* padding: 10px 5px; */
  margin: 10px 5px 10px 25px;
  float: left;
}

#msform .action-button-previous:hover,
#msform .action-button-previous:focus {
  background-color: #000000;
}

.head-card {
  z-index: 0;
  border: none;
  position: relative;
}

.fs-title {
  font-size: 25px;
  color: #007bff;
  margin-bottom: 15px;
