


/* AttendanceHeader.css */
.attendance-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3b3f5c;
    color: white;
    padding: 10px;
    /* border-radius: 8px; */
}

.stats {
    font-size: 18px;
}

.legend {
    display: flex;
}

.legend-item {
    margin-left: 20px;
    padding: 5px 10px;
    border-radius: 5px;
}

.present {
    background-color: #28a745;
}

.absent {
    background-color: #dc3545;
}

.holiday {
    background-color: #6c757d;
}

.leave {
    background-color: #ffc107;
}

/* AttendanceCalendar.css */
.calendar-container-attendance {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-left: 50px;
}

.attendance-month {
    width: 30%;
    background-color: white;
}

.attendance-month table {
    width: 100%;
    border-collapse: collapse;
}

.attendance-month th, .attendance-month td {
    text-align: center;
    padding: 5px;
    border: 1px solid #ddd;
}

.attendance-month td {
    height: 50px;
    cursor: pointer;
}

.attendance-month td.present {
    background-color: #e6ffe6; /* Example color for present days */
    border-radius: 50%;
}

