    content: "\f05a";
  }
  #progressbar #parents:before {
    font-family: FontAwesome;
    content: "\f007"; /* Example icon code for a parent-like icon */
  }
  #progressbar #confirm:before {
    font-family: FontAwesome;
    content: "\f00c";
  }
  
  #progressbar li:before {
    width: 50px;
    height: 50px;
    line-height: 45px;
    display: block;
    font-size: 20px;
    color: #ffffff;
    background: lightgray;
    border-radius: 50%;
    margin: 0 auto 10px auto;
    padding: 2px;
    cursor: pointer;
  }
  
  /* #progressbar li:after {
    content: "";
    width: 100%;
    height: 2px;
    background: lightgray;
    position: absolute;
    left: -50;
    top: 25px;
    z-index: -1;
  } */
  
  #progressbar li:after {
    content: "";
    width: 150%;
    height: 2px;
    background: lightgrey;
    color: #000000;
    position: absolute;
    left: -50%;
    top: 25px;
    z-index: -1; /*put it behind the numbers*/
  }
  
  #progressbar li.active:before,
  #progressbar li.active:after {
    background: #007bff;
  }
  
  .progress {
    height: 20px;
  }
  
  .progress-bar {
    background-color: #007bff;
  }
  
  .fit-image {
    width: 100%;
    object-fit: cover;
  }
  
  .optional {
    color: #6c757d; /* Gray color, for example */
    font-style: italic;
    font-size: 13px;
  }
  