.active-tab::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid white; /* Arrow color */
  position: absolute;
  right: -20px; /* Adjust this based on your layout */
  top: 50%;
  transform: translateY(-50%);
}
