.calendar-container {
  max-width: 300px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
}

.month-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-weight: bold;
}

.days-of-week,
.days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  text-align: center;
  gap: 5px;
}

.day {
  padding: 5px;
  border-radius: 5px;
}

.day.null {
  visibility: hidden;
}

.day.current {
  background-color: #e0f7fa;
  border: 1px solid #c100bb71;
}

button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
.news-ticker {
  height: 200px;
  overflow: hidden;
  position: relative;
}

.news-container {
  position: absolute;
  white-space: nowrap;
  animation: scroll 10s linear infinite;
}

.news-item {
  display: flex;
  flex-direction: column;
  border: 2px solid yellowgreen;
  margin:10px;
  width: 450px;
  padding: 10px;
  font-size: 14px;
}
/* Dashboard.css */

.upcoming-event-item {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.upcoming-event-icon {
  font-size: 1.5rem; /* Adjust size as needed */
  margin-right: 0.75rem;
}

.upcoming-event-content {
  flex: 1;
}

.upcoming-event-title {
  font-size: 1.25rem;
  margin: 0;
}

.upcoming-event-description {
  margin: 0;
  font-size: 1rem;
}


.events-widget {
  background-color: white;
  border: 1px solid #e0e0e0;
  padding: 16px;
  border-radius: 4px;
}

.events-widget .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.events-widget .header h2 {
  font-size: 18px;
  margin: 0;
  font-weight: bold;
}

.events-widget .header .view-more {
  font-size: 14px;
  color: #007bff;
  cursor: pointer;
}

.events-widget .event {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid grey;
}

.events-widget .event:last-child {
  border-bottom: none;
}

.events-widget .event-date {
  background-color: #d32f2f;
  color: white;
  width: 50px;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  margin-right: 16px;
}

.events-widget .event-date span {
  display: block;
}

.events-widget .event-date .day {
  font-size: 18px;
  font-weight: bold;
}

.events-widget .event-date .month {
  font-size: 12px;
}

.events-widget .event-details {
  flex-grow: 1;
}

.events-widget .event-title {
  font-size: 16px;
  margin: 0;
}

.events-widget .calendar-footer {
  display: flex;
  justify-content: center;
  padding: 8px 0;
}

.events-widget .calendar-footer button {
  background: none;
  border: none;
  color: #007bff;
  font-size: 14px;
  cursor: pointer;
}

/* Dashboard.css */

.achievement-item {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.achievement-icon {
  font-size: 1.5rem; /* Adjust size as needed */
  margin-right: 0.75rem;
}

.achievement-title {
  font-size: 1.25rem;
  margin: 0;
}

/* Adjustments for overall styling */
.list-group-item {
  padding: 1rem;
}

.activeTab{
    background-color: white;
}
.birthday-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.birthday-item img {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.birthday-details {
  margin-left: 10px;
}

.birthday-date {
  margin-left: auto;
  text-align: right;
}

.nav-link.activeTab {
  background-color: rgb(232, 106, 209);
  color: white;
}

.card-tools .nav-pills .nav-link {
  cursor: pointer;
}

