.attendance-report {
  display: flex;
  /* justify-content: space-between; */
  gap: 20px; /* Optional: Adds space between the two sections */
  margin: 20px 0;
}

.attendance-summary {
  flex: 0 0 35%; /* Takes 40% of the row */
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.daily-attendance {
  flex: 0 0 60%; /* Takes 60% of the row */
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

