.staff-on-leave-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
    padding-left: 0;
    margin: 0;
}

.staff-item {
    flex: 0 0 48%; /* Take up about 48% of the container width, adjust as needed */
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    padding-right: 10px; /* Optional: spacing between items */
    box-sizing: border-box;
}

.staff-info {
    display: flex;
    flex-direction: column;
}

.staff-name {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 5px;
}

.staff-role {
    font-size: 14px;
    color: #6c757d;
}

.clearfix {
    width: 100%;
}


@keyframes scroll {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(-100%);
  }
}

