/* Ensure the input group is a flex container */
.input-groupp {
  display: flex;
  align-items: center;
  width: 500px;
}
.custom-file-input {
  width: 3px;
  height: 34px;
}

.custom-file-label {
  min-width: 0;
}


.validation-errors ul {
  list-style-type: disc;
  margin-left: 20px;
}

 *
 .error-row {
  color: red !important;
}

.error-cell {
  color: red;
  border: 1px solid red;
}


.table-responsive {
  overflow-x: auto;
}

.custom-datatable .p-datatable-tbody > tr {
  height: auto;
  padding-top: 10px;
  padding-bottom: 10px; 
}

.custom-datatable .p-datatable-tbody > tr > td {
  padding-left: 10px;
  padding-right: 10px; 
  text-align: center; 
}

.custom-datatable .p-datatable-thead > tr > th {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px; 
  padding-bottom: 10px;
  text-align: center;
}

.swal2-actions .btn {
  margin: 0 10px;
}

/* Main container for validation errors */
.validation-errors {
  padding: 15px;
  border-radius: 5px;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb; 
  color: #721c24;
  margin-top: 20px;
}

/* Heading for validation errors */
.validation-errors h4 {
  font-size: 1.2em;
  margin-bottom: 10px;
}

/* List for each validation error */
.validation-errors ul {
  list-style-type: none;
  padding: 0;
}

/* Each error item */
.validation-errors li {
  padding: 5px 0;
}

/* Error in specific row */
.validation-errors .row-error {
  font-weight: bold;
  color: #155724; 
}

/* Inner list items */
.validation-errors .error-detail {
  margin-left: 20px;
  color: #856404;
}



